      numero_T: "1234567",
      tipo_falta: "1"
    };
  },
  methods: {
    Ver() {
      console.log(this.dia);
      return null;
    },
    Actualizar() {
      var today = new Date();
      var dd = today.getDate();
      var mm = today.getMonth() + 1;
      var yyyy = today.getFullYear();
      if (dd < 10) {
        dd = "0" + dd;
      }
      if (mm < 10) {
        mm = "0" + mm;
      }
      today = yyyy + "-" + mm + "-" + dd;
      this.sol_dia = today;
    },
    Momento() {
      var today = new Date();
      var dd = today.getDate();
      var mm = today.getMonth() + 1;
      var yyyy = today.getFullYear();
      var hora = today.getHours();
      var minutos = today.getMinutes();
      var segundos = today.getSeconds();
      if (dd < 10) {
        dd = "0" + dd;
      }
      if (mm < 10) {
        mm = "0" + mm;
      }
      if(hora < 10){
        hora = "0"+hora;
      }
      if(minutos < 10){
        minutos = "0"+minutos;
      }
      if(segundos < 10){
        segundos="0"+segundos;
      }
      return yyyy + "" + mm + "" + dd+""+hora+""+minutos+""+segundos+"";
      
    },
    Asignar_ID(){
      this.tipo_falta = (this.tipo_falta.length < 2)? "0"+this.tipo_falta : this.tipo_falta;
      this.id_sol = this.numero_T+this.tipo_falta+this.Momento();
      console.log(this.id_sol.length);
    },
    Cargar_Datos() {
      this.Actualizar();
      this.Asignar_ID();
